





















































import Vue from "vue";
import Component from "vue-class-component";
import mixin from "../../config/mixin";
import { Action } from "vuex-class";
import { Toast } from "vant";
import comhead from "../../components/Comhead.vue";

@Component({
  components: { comhead },
  mixins: [mixin]
})
export default class goodscomment extends Vue {
  goodsId = "";
  status = 0;
  commentList = [];

  stars = [
    {
      src: require("../../assets/image/灰色星星.png"),
      active: false
    },
    {
      src: require("../../assets/image/灰色星星.png"),
      active: false
    },
    {
      src: require("../../assets/image/灰色星星.png"),
      active: false
    },
    {
      src: require("../../assets/image/灰色星星.png"),
      active: false
    },
    {
      src: require("../../assets/image/灰色星星.png"),
      active: false
    }
  ];

  tab(index) {
    this.status = index;
    this.getcommentlist();
  }
  scale = 0;
  pageindex = 10;
  loading = false;
  getProductDetail() {
    Vue.prototype.$reqFormPost(
      "/goods/front/detail",
      {
        userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .userId,
        token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
          .token,
        goodsId: this.goodsId
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          Toast(res.data.message);
          return;
        }
        console.log("---------", res.data);

        this.scale = res.data.data.scale;
        this.getstars(res.data.data.commentStar);
      }
    );
  }
  loadMore() {
    this.loading = true;
    let self = this;
    setTimeout(() => {
      this.pageindex += 10;
      this.getcommentlist();
    }, 1000);
  }
  getcommentlist() {
    Vue.prototype.$reqFormPost(
      "/comment/list",
      {
        goodsId: this.goodsId,
        status: this.status,
        page: 0,
        pageSize: this.pageindex
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );

          Toast(res.data.message);
          return;
        }
        this.commentList = res.data.data.commentList;
        if (res.data.data.commentList.length != this.pageindex) {
          this.loading = false;
        }
      }
    );
  }
  getstars(num) {
    for (var i = 0; i < num; i++) {
      this.stars[i].src = require("../../assets/image/星星.png");
      this.stars[i].active = true;
    }
  }

  handlePX(CssName, PxNumber) {
    return (
      CssName +
      ":" +
      this.$store.getters[Vue.prototype.MutationTreeType.SYSTEM].availWidth /
        750 *
        PxNumber +
      "px;"
    );
  }

  mounted() {
    this.goodsId = this.$route.query.goodsId;
    this.getcommentlist();
    console.log("商品评论页");
    this.getProductDetail();
  }
}



